@import url('https://fonts.googleapis.com/css?family=Labrada|Wallpoet|VT323|Ubuntu|Julee|Lora|Kalam|Merienda');
.App{
  padding-left: 10vw;
  padding-right: 10vw;
  display: grid;
  grid-template-rows: 3cm auto;
  row-gap: 1cm;
}
.addTodoForm{
  display: flex;
  column-gap: .2cm;
}
.addTodoInput{
  width: 8cm;
  height: 1cm;
  font-size: 25px;
  border: none;
  background-color: #D6F1FF;
  border-radius: 10px;
}
.addTodoInput:hover{
  border: 1px solid black;
}
.signIn{
  display: flex;
  column-gap: 3vw ;

  flex-wrap: wrap;
}
.leftSignIn{
  width: 39vw;
  /* border: 1px solid black; */
 
}
.rightSignIn{
  width: 37vw;
  /* border: 1px solid black; */
}
.tasksSvg{
  width: 37vw;
}
.tagLine{
  font-size: 40px;
  margin: 5px 0px;
  font-weight: 600;
  font-family: Lora;
}
.headLine{
  font-size: 50px;
  margin: 5px 0px;
  font-weight:600;
  font-family: Lora;
  color: blueviolet;
}
.addTodoButton{
  width: 2cm;
  height: 1.1cm;
  background-color: blueviolet;
  color: whitesmoke;
  font-size: 25px;
  font-weight: bold;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.todoList{
  display: flex;
  column-gap: .3cm;
  align-items: center;
  
}
.delTodo{
  width: .7cm;
  height: .7cm;
  cursor: pointer;
}
.todoName{
  margin: 5px 0px;
  text-align: left;
}
.sign-out{
  width: 2.5cm;
  height: .8cm;
  font-size: 16px;
  background-color: #28282b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.sign-in{
  width: 8cm;
  height: 1.2cm;
  font-size: 25px;
  background-color: blueviolet;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  /* margin-left: 20%; */
}
.userPhoto{
  width: 1.5cm;
  height: 1.5cm;
  border-radius: 50px;
}
.userPhotoSection{
  display: flex;
 
  align-items: center;
  column-gap: .2cm;
}
.subTagLine{
  font-size: 30px;
  margin: 5px 0px;
  font-weight: 600;
  font-family: Lora;
}
.logoText{
  font-family: Julee;
  font-size: 50px;
}

@media screen and (max-width:1350px){
 .tagLine{
  font-size: 35px;
 }
 .headLine{
  font-size: 45px;
 }
 .subTagLine{
  font-size: 25px;
 }
}

@media screen and (max-width:1200px){
 
  .tagLine{
    font-size: 30px;
   }
   .headLine{
    font-size: 40px;
   }
   .subTagLine{
    font-size: 20px;
   }
}
@media screen and (max-width:1000px){
     .tasksSvg{
    padding-top: 20%;
  }
}
@media screen and (max-width:800px){
  .rightSignIn{
    display: none;
  }
  .leftSignIn{
    width: 80vw;
  }
}

@media screen and (max-width:400px){
  .tagLine{
    font-size: 25px;  
  }
  .headLine{
    font-size: 35px;
   }
   .subTagLine{
    font-size: 17px;
   }
   .sign-in{
      width: 6cm;
      font-size: 22px;
   }

}
@media screen and (max-width:300px){
  .headLine{
    font-size: 30px;
   }
   .sign-in{
    width: 4cm;
    font-size: 17px;
   }
   .logoText{
    font-size: 45px;
   }
}
@media screen and (max-width:250px){
  .logoText{
    font-size: 40px;
  }
}